.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid rgb(143, 140, 140);
  padding: 12px;
  margin: 12px 0;
  border-radius: 5px;
  /* width: clamp(20vw, 500px, auto); */
  /* border: 2px solid red; */
  color: black;
  height: fit-content;
  /* width: 20rem; */
  width: min(40vw, 250px);
  /* overflow: hidden; */
  transition: 0.3s ease;
  overflow: hidden;
  background-color: #0303031c;
  & #more-info-btn {
    max-height: 0;
    cursor: pointer;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease, visibility 0.5s ease, max-height 0.5s ease;
    border: none;
    border-radius: 5px;
    color: rgb(1, 51, 51);
    background-color: #9c9a98;
    /* display: none; */
  }
  & img {
    /* background-image: url("https://www.shutterstock.com/search/different-fruits-outline"); */
    object-fit: contain;
    margin-bottom: 10px;
    border-radius: 5px;
    /* background-color: rgb(255, 255, 255); */
    padding: 12px;
    height: 200px;
    width: 100%;
  }
  & .originaPrice {
    color: red;
    text-decoration: line-through;
    margin-right: 7px;
  }
  & span {
    color: green;
    justify-content: center;
    align-items: center;
  }
  & h2 {
    font-size: 1.3rem;
    overflow: hidden;
    color: rgb(0, 0, 0);
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  & h3 {
    font-size: 1rem;
    color: #5b5c5c;
  }
  &:hover {
    /* position: sticky; */
    /* left: 0; */
    /* right: 0; */
    z-index: 100;
    transform: scale(1.03);
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.247);
    #more-info-btn {
      max-height: fit-content;
      opacity: 1;
      visibility: visible;
      transition: opacity 0.5s ease, visibility 0.5s ease, max-heigth 0.5s ease;
      /* display: block; */
    }
  }
  /* overflow: visible; */
  /* color: rgb(0, 0, 0); */
}

.cardContainer {
  /* border: 2px solid blueviolet; */
  margin: 1em;
  /* display: grid; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  /* gap: 12px; */
  /* overflow: hidden; */
}

