.searchArea {
  display: flex;
  margin: 12px;
  justify-content: center;
  align-items: center;
  & input {
    color: rgb(54, 54, 54);
  }
  & input::placeholder {
    color: black;
  }
  & button {
    transition: 0.3s ease;
    /* border: 1px solid rgb(255, 255, 255); */
    border: none;
    border-radius: 5px;
    font-size: 1.5rem;
    color: hsl(0, 0%, 100%);
    background-color: rgb(48, 75, 69);
    /* background-color: transparent; */
    &:hover {
      background-color: rgb(32, 83, 81);
      cursor: pointer;
    }
  }
}

