.spining {
  position: fixed;
  top: 30%;
  border: 2px solid #ffffff;
  border-top: 2px solid rgb(146, 141, 141);
  border-radius: 50%;
  width: 70px;
  height: 70px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.productPage {
  z-index: 100;
  height: auto;
  width: 70vw;
  background-color: #5b5c5c;
}
